body {
  margin: 0;
}

.stars {
  display: flex;
  justify-items: center;
  align-items: center;
  height: 15px;
}

.stars__item {
  width: 15px;
  height: 15px;
  margin-right: 4px;
  background: url(images/star.svg) no-repeat center;
}

.stars--1 .stars__item:nth-child(-n+1),
.stars--2 .stars__item:nth-child(-n+2),
.stars--3 .stars__item:nth-child(-n+3),
.stars--4 .stars__item:nth-child(-n+4),
.stars--5 .stars__item:nth-child(-n+5) {
  background: url(images/star-active.svg) no-repeat center;
}
